.banner{
    //稱寬高的方法，除了直接用內部元素撐開，就只剩設定寬高嗎？
    background: url('../images/home-header.png') no-repeat center;
    background-size: cover;
    height: 787px;
    @include pad {
        // height: 396px;
    }
    @include mobile {
        background: url('../images/home-header-sm.png') no-repeat center;
        background-size: cover;
        height: 396px;
    }
    &-subtitle {
        @include pad {
          font-size: 26px;
        }
        @include mobile {
          font-size: 16px;
        }
      }
    &-title {
        @include pad {
            font-size: 48px;
        }
        @include mobile {
            font-size: 24px;
        }
      }
    &-btn{
        padding: 5px 8px 5px 8px;
        @include link-layout;
    }
}


.service{
    &-title {
        @include pad {
            font-size: 36px;
        }
        @include mobile {
            font-size: 24px;
        }
      }
    
    ul{ 
        @include pad {
            flex-wrap: wrap;
        }

        li{
            width: 25%;
            @media (max-width: 832px){
                width: 48%;
                margin: 0 auto;
            }
            @include mobile {
                width: 98%;
            }
        }

        p{
            text-align: left;
        }
    }
}

.series{
    &-title {
        @include pad {
            font-size: 36px;
        }
        @include mobile {
            font-size: 24px;
        }
      }
    ul{
        @include desktop-l {
            flex-direction: column;
            align-items: center;
        }
    }
    li {
        width: 648px;
        @include pad {
          width: 98%;
          margin-bottom: 24px;
         }
         img {
           @include pad {
             width: 336px;
             height: 336px;
           }
         }
        }
}

.cooperation {
    background: url('../images/index-bg-sm.png') no-repeat center fixed;
    background-size: cover;
    &-title {
        @include pad {
            font-size: 36px;
        }
        @include mobile {
            font-size: 24px;
        }
      }
    &-list{
        @include pad {
            flex-direction: column;
        }
        @include mobile {
           
        }
    }
    &-card{
        @include pad {
            margin-bottom: 24px;
        }
        @include mobile {
            margin-bottom: 32px;
        }
    }
    &-img{
        @include mobile {
            .desktop-img{
                display: none;
            }
            .mobile-img{
                //為什麼加了margin還是有神秘留白空間
                margin: 0 auto;
                display: inline-block;
            }
        }
    }
}
